import UsersModel from '../models/user';
import PasswordResetRequestModel from '../models/passwordresetrequest';

export default async function resetPassword(userId, newPassword, uid) {
  const Users = UsersModel();
  const PasswordResetRequests = PasswordResetRequestModel();

  const updatedUser = await Users.update(
    { userId },
    { password: newPassword },
    { new: true }
  );
  await PasswordResetRequests.deleteOne({ code: uid });
  return updatedUser;
}
